import React from 'react';
import styled from '@emotion/styled';
import LazyHighlight from './lazyHighlight';
import { fonts } from '../../../design-system';
import Dracula from 'prism-react-renderer/themes/dracula';
import normalize from './normalize';
import Copy from '../CopyButton';

const CopyWrapper = styled.div`
  flex-grow: 1;
  display: flex;
  justify-content: flex-end;
  padding-right: 2rem;
`;

const StyledWrapper = styled.div`
  position: relative;
  overflow: auto;
  border-radius: 8px;
  background: #282a36;
  color: white;
  box-shadow: rgba(0, 0, 0, 0.55) 0 8px 16px;
  font-family: ${fonts.monospace};
  margin: 1.5rem 0;
`;

const StyledCarbonTitle = styled.div`
  padding-left: 1rem;
  color: rgba(255, 255, 255, 0.7);
  font-size: 14px;
  font-weight: 500;
`;

const StyledCarbon = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  padding: 16px;
  border-bottom: 1px solid rgba(255, 255, 255, 0.1);
  > .circle {
    width: 12px;
    height: 12px;
    border-radius: 50%;
    margin: 0 6px 0 0;
    &.red {
      background: #ff5f56;
      border: 0.5px solid #e0443e;
    }

    &.yellow {
      background: #ffbd2e;
      border: 0.5px solid #dea123;
    }

    &.green {
      background: #27c93f;
      border: 0.5px solid #1aab29;
    }
  }
`;

// get params from code classname
const getParams = (name) => {
  let params = { title: '' };
  if (!name) {
    return ['text', params];
  }
  const language = name.match(/language-(\w+)/)[1];
  const [_, paramsStr = ''] = name.split(':');
  if (paramsStr.length > 0) {
    params = paramsStr.split('&').reduce((prev, param) => {
      const [key, value] = param.split('=');
      prev[key] = value;
      return prev;
    }, {});
  }
  return [language, params];
};

export const CodeBlock = ({ children }) => {
  const className = children.props ? children.props.className : '';
  const [language, { title = '' }] = getParams(className);
  const [content, highlights] = normalize(
    children.props && children.props.children ? children.props.children : children,
    className
  );
  return (
    <StyledWrapper>
      <StyledCarbon>
        <div className="circle red"></div>
        <div className="circle yellow"></div>
        <div className="circle green"></div>
        {title && title.length > 0 && <StyledCarbonTitle>{title}</StyledCarbonTitle>}
        <CopyWrapper>
          <Copy content={content} />
        </CopyWrapper>
      </StyledCarbon>
      <LazyHighlight code={content} language={language} theme={Dracula}>
        {({ tokens, getLineProps, getTokenProps }) => (
          <>
            <div className="gatsby-highlight">
              <pre className={`language-${language}`}>
                <code className={`language-${language}`}>
                  {tokens.map((line, i) => {
                    const lineProps = getLineProps({ line, key: i });
                    const className = [lineProps.className]
                      .concat(highlights[i] && `gatsby-highlight-code-line`)
                      .filter(Boolean)
                      .join(' ');
                    return (
                      <div key={i} {...Object.assign({}, lineProps, { className })}>
                        {line.map((token, idx) => (
                          <span key={idx} {...getTokenProps({ token, key: idx })} />
                        ))}
                      </div>
                    );
                  })}
                </code>
              </pre>
            </div>
          </>
        )}
      </LazyHighlight>
    </StyledWrapper>
  );
};
