/**@jsx jsx */
import { jsx } from 'theme-ui';
import React from 'react';
import { fontSizesRem, fontWeights } from '../../design-system';

import kebabCase from 'lodash/kebabCase';
import styled from '@emotion/styled';

const escapseURI = (url) => url.replace(/\//, '');
const convertURI = (url) => kebabCase(escapseURI(url));

const Headings = (Tag) => ({ children }) => {
  const id = convertURI(children);
  return (
    <Tag id={id}>
      <a
        href={'#' + id}
        sx={{
          textDecoration: 'none',
          transition: 'color ease-in .1s',
          color: 'inherit',
          '&:hover': {
            color: 'primary',
            '::after': {
              content: '"#"',
              paddingLeft: 1,
            },
          },
        }}
      >
        {children}
      </a>
    </Tag>
  );
};

const StyledH1 = styled.h1`
  font-size: ${fontSizesRem[8]};
  font-weight: ${fontWeights.heading};
  counter-reset: chapter;
`;

const StyledH2 = styled.h2`
  font-size: ${fontSizesRem[7]};
  font-weight: ${fontWeights.heading};
  counter-reset: section;
  &::before {
    content: counter(chapter);
    counter-increment: chapter;
  }
`;

const StyledH3 = styled.h3`
  font-size: ${fontSizesRem[6]};
  font-weight: ${fontWeights.heading};
  counter-reset: subsection;
  &::before {
    content: counter(chapter) '.' counter(section);
    counter-increment: section;
  }
`;

const StyledH4 = styled.h4`
  font-size: ${fontSizesRem[5]};
  font-weight: ${fontWeights.heading};
  counter-reset: subsubsection;
  &::before {
    content: counter(chapter) '.' counter(section) '.' counter(subsection);
    counter-increment: subsection;
  }
`;

const StyledH5 = styled.h5`
  font-size: ${fontSizesRem[4]};
  font-weight: ${fontWeights.heading};
  counter-reset: subsubsubsection;
  &::before {
    content: counter(chapter) '.' counter(section) '.' counter(subsection) '.'
      counter(subsubsection);
    counter-increment: subsubsection;
  }
`;

const StyledH6 = styled.h6`
  font-size: ${fontSizesRem[3]};
  font-weight: ${fontWeights.heading};
  &::before {
    content: counter(chapter) '.' counter(section) '.' counter(subsection) '.'
      counter(subsubsection) '.' counter(subsubsubsection);
    counter-increment: subsubsubsection;
  }
`;

export const H1 = Headings(StyledH1);
export const H2 = Headings(StyledH2);
export const H3 = Headings(StyledH3);
export const H4 = Headings(StyledH4);
export const H5 = Headings(StyledH5);
export const H6 = Headings(StyledH6);
