import React, { useState } from 'react';
import styled from '@emotion/styled';
import copy2Clipboard from '../utils/copy-to-clipboard';
import { Copy } from 'react-feather';

const delayResolve = (duration) =>
  new Promise((resolve) => {
    return setTimeout(resolve, duration);
  });

const StyledButton = styled.div`
  position: relative;
  color: white;
  cursor: pointer;
  width: 24px;
  height: 24px;
  &[disabled] {
    cursor: not-allowed;
  }
`;

const TipInner = styled.div`
  position: relative;
  background: #3e3e40;
  border-radius: 8px;
  padding: 1px;
`;

const Tip = styled.div`
  position: absolute;
  left: 50%;
  width: 70px;
  margin-left: -35px;
  text-align: center;
  &::before {
    content: '';
    position: absolute;
    left: 30px;
    top: -5px;
    width: 0;
    height: 0;
    border-style: solid;
    border-color: #3e3e40 transparent;
    border-width: 0 6px 6px;
  }
`;

const CopyButton = ({ content, duration = 2000 }) => {
  const [copied, setCopied] = useState(false);
  const [mouseIn, setMouseIn] = useState(false);
  return (
    <StyledButton
      disabled={copied}
      name="copy to clipboard"
      onClick={async () => {
        await copy2Clipboard(content);
        setCopied(true);
        await delayResolve(duration);
        setCopied(false);
      }}
      onMouseEnter={(e) => {
        setMouseIn(true);
      }}
      onMouseLeave={(e) => {
        setMouseIn(false);
      }}
    >
      <Copy />
      <Tip style={{ display: mouseIn ? 'block' : 'none' }}>
        <TipInner>{copied ? 'Copied' : 'Copy'}</TipInner>
      </Tip>
    </StyledButton>
  );
};

export default CopyButton;
