import React from 'react';
import loadable from '@loadable/component';

// add to eslintignore as dynamic import is not supported now in eslint
// https://github.com/eslint/eslint/issues/11486
// https://github.com/eslint/eslint/issues/11514
const LazyHighlight = loadable(async () => {
  /* eslint-disable */
  const Module = await import(`prism-react-renderer`);
  /* eslint-enable */
  const Highlight = Module.default;
  const DefaultProps = Module.defaultProps;
  return (props) => <Highlight {...DefaultProps} {...props}></Highlight>;
});

export default LazyHighlight;
