import React from 'react';
import { MDXProvider } from '@mdx-js/react';
import * as DesignSystem from '../src/components/CustomMDX';

const components = {
  h1: DesignSystem.H1,
  h2: DesignSystem.H2,
  h3: DesignSystem.H3,
  h4: DesignSystem.H4,
  h5: DesignSystem.H5,
  h6: DesignSystem.H6,
  p: DesignSystem.Paragraph,
  a: DesignSystem.StyledExternalLink,
  pre: ({ children }) => <DesignSystem.CodeBlock>{children}</DesignSystem.CodeBlock>,
  inlineCode: DesignSystem.InlineCode,
  blockquote: DesignSystem.Blockquote,
};

export const wrapRootElement = ({ element }) => (
  <MDXProvider components={components}>{element}</MDXProvider>
);
