import preval from 'preval.macro';

const fs = preval`
let sizes = new Array(18).fill(8);
for (let i = 0; i < 18; i++) {
  sizes[i + 1] = sizes[i] + (parseInt((i - 2) / 4) + 1) * 2;
}
sizes.splice(0, 2);
const px = sizes.map((s) => s + 'px');
const rem = sizes.map((s) => s / 16 + 'rem');
module.exports = {sizes,px,rem}
`;

export const fontSizesRaw = fs.sizes;
export const fontSizesPx = fs.px;
export const fontSizesRem = fs.rem;
